@import url(https://fonts.googleapis.com/css2?family=Sen:wght@400;700;800&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: black;
  font-family: Sen, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: black;
  width: 100%;
}

@media screen and (min-width: 1260px) {
  .app {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
  }
}

.myName {
  color: white;
  font-weight: bold;
  font-size: 68px;
}

.myIntro {
  color: white;
  font-size: 23px;
  font-weight: bold;
  margin-top: 10px;
  width: 400px;
}

.lightName {
  color: rgba(255, 255, 255, 0.5);
}

.leftView {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition-duration: 1.5s;
  transition-property: left;
  position: relative;
  left: -20%;
  margin: 20px;
}

.leftView.animateStart {
  left: 0;
}

.myPhoto {
  height: 250px;
  width: 250px;
  border-radius: 50%;
}

.bigImage {
  height: 450px;
  width: 450px;
  border-radius: 50%;
}

.myPhotoWrapper {
  justify-content: center;
  align-items: center;
  align-self: center;
  margin-top: 20px;
}

.rightView {
  position: relative;
  right: -30%;
  margin: 100px;
  transition-duration: 1.5s;
  transition-property: right;
}

.rightView.animateStartAgain {
  right: 0;
}

@-webkit-keyframes left-to-right {
  0% {
    left: 0;
    width: 0%;
  }

  50% {
    width: 35%;
  }

  100% {
    left: 100%;
    width: 0%;
  }
}

@keyframes left-to-right {
  0% {
    left: 0;
    width: 0%;
  }

  50% {
    width: 35%;
  }

  100% {
    left: 100%;
    width: 0%;
  }
}

@-webkit-keyframes right-to-left {
  0% {
    right: 0;
    width: 0%;
  }

  50% {
    width: 35%;
  }

  100% {
    right: 100%;
    width: 0%;
  }
}

@keyframes right-to-left {
  0% {
    right: 0;
    width: 0%;
  }

  50% {
    width: 35%;
  }

  100% {
    right: 100%;
    width: 0%;
  }
}

.experienceWrapper {
  width: 100%;
  background-color: aquamarine;
  margin-bottom: 20px;
}

@media screen and (min-width: 1260px) {
  .experienceWrapper {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
  }
}

.skills-section {
  padding: 3rem 0;
  background: #262626;
  color: #e6e6e6;
}

.skills-div {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.skills-section h2 {
  color: #e6e6e6;
  letter-spacing: 0.2rem;
  font-weight: bold;
  margin-left: 1rem;
  font-size: 70px;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
}

.skills-section .container h3 {
  margin-left: 1rem;
}

.skills-card {
  background: #1a1a1a;
  color: #e6e6e6;
  width: 15rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  border-radius: 1rem;
  margin: 1rem 0.5rem;
  transform: scale(0.98);
}

.skills-card:hover {
  transform: scale(1);
}

.skills-card img {
  height: 11rem;
  width: 11rem;
  margin-bottom: 1rem;
}

